<template>
    <div v-show="isSingleMode">
        <div class="table-box m-b-10 p-b-5" v-loading="listLoading">
            <el-form :inline="true" class="demo-form-inline">
                <el-form-item :label="$t('label.dockerStatus')">
                    <span class="c-success bold m-r-10" v-if="dockerAvailable">{{$t('global.running')}}</span>
                    <span class="c-danger bold m-r-10" v-else>{{$t('global.stop')}}</span>
                </el-form-item>
                <el-form-item :label="$t('label.swarmSwitch')" v-show="dockerAvailable">
                    <el-switch v-model="swarmAvailable" @change="actionSwarm('switchSwarmCapacity')"></el-switch>
                </el-form-item>
            </el-form>
        </div>
        <div class="p-r table-box">
            <div class="search" v-show="isShowSearchInput"> 
                <el-input size="small" class="w-250 m-l-10" v-model="keywords" :placeholder="$t('dockerControl.keywordHolder')" >  
                </el-input>
                <i class="fa fa-search search-icon" aria-hidden="true"></i>
            </div>
            <el-tabs v-model="activeName" @tab-click="handleClick">
                <el-tab-pane name="compose">
                    <span slot="label">
                        <i class="fa fa-sellsy m-r-5" aria-hidden="listLoading"></i>
                        {{$t('dockerControl.stack')}}
                        <el-tooltip class="item" effect="light" :content="$t('dockerControl.swarmTooltip')" placement="top-start" >
                            <i class="fa fa-question-circle tooltip m-l-5" aria-hidden="true"></i>
                        </el-tooltip>
                    </span>
                    <el-table
                    :data="composeTableData.filter(data => !keywords || data.name.toLowerCase().includes(keywords.toLowerCase()))"
                    tooltip-effect="dark"
                    style="width: 100%"
                    v-loading="listLoading">
                        <el-table-column
                        label=""
                        width="100"
                        >
                            <template slot-scope="scope">
                                <div>
                                    <img src="@/assets/imgs/compose.png" alt="no img" style="width:48px"/>
                                </div>
                            </template>
                        </el-table-column>
                        <el-table-column
                        :label="$t('dockerControl.projectName')"
                        min-width="150"
                        sortable
                        >
                            <template slot-scope="scope"> 
                                <div class="pointer url" @click="goContainer(scope.row)" 
                                v-if="(dockerAvailable&&scope.row.type == 'Compose')
                                ||(scope.row.type=='Swarm'&&swarmAvailable)">
                                    {{scope.row.name}}
                                </div>
                                <div v-else>
                                    {{scope.row.name}}
                                </div>
                            </template>
                        </el-table-column>
                        <el-table-column
                        prop="version"
                        :label="$t('dockerControl.version')"
                        min-width="120"
                        sortable>
                        </el-table-column>
                        <el-table-column
                        prop="type"
                        :label="$t('dockerControl.type')"
                        min-width="120"
                        sortable>
                        </el-table-column>
                        <el-table-column
                        :label="$t('dockerControl.origin')"
                        min-width="120"
                        sortable>
                            <template slot-scope="scope"> 
                                <div>
                                    <span  v-if="scope.row.control == 'Limited'">
                                        {{$t('dockerControl.external')}}
                                        <el-tooltip class="item" effect="light" :content="$t('dockerControl.externalTooltip')" placement="top-start" >
                                            <i class="fa fa-question-circle tooltip" aria-hidden="true"></i>
                                        </el-tooltip>
                                    </span>
                                    <span v-else>
                                        {{$t('dockerControl.internal')}}
                                    </span>
                                </div>
                            </template>
                        </el-table-column>

                        <el-table-column
                        :label="$t('dockerControl.state')"
                        min-width="120"
                        align="center"
                        >
                            <template slot-scope="scope"> 
                                <span class="c-success m-l-2 m-r-2">{{scope.row.dockerrunning}}</span>
                                /<span class="c-primary m-l-2">{{scope.row.dockernum}}</span>
                            </template>
                        </el-table-column>
                        <el-table-column
                            :label="$t('dockerControl.view')"
                            min-width="100">
                            <template slot-scope="scope"> 
                                <div>
                                    <i class="fa fa-eye pointer c-darkgray" aria-hidden="true"  v-if="scope.row.control == 'Limited'"></i>
                                    <i class="fa fa-eye pointer c-primary" aria-hidden="true" @click="showYmlContent(scope.row)" v-else></i>
                                </div>
                            </template>
                        </el-table-column>
                        <el-table-column
                            :label="$t('dockerControl.action')"
                            min-width="250">
                            <template slot-scope="scope"> 
                                <div>
                                    <el-button-group v-if="scope.row.type == 'Swarm'">
                                        <el-button type="success" size="mini"
                                        :disabled="scope.row.control == 'Limited'||!swarmAvailable"  
                                        @click="composeAction('startSwarm', scope.row)">
                                        <i class="fa fa-play m-r-5"></i>{{$t('global.start')}}</el-button>
                                        <el-button type="danger" size="mini"
                                        :disabled="scope.row.control == 'Limited'||!swarmAvailable" 
                                        @click="composeAction('stopSwarm', scope.row)">
                                        <i class="fa fa-stop m-r-5"></i>{{$t('global.stop')}}</el-button>
                                        <el-button type="primary" size="mini"
                                        :disabled="scope.row.control == 'Limited'||!swarmAvailable"    
                                        @click="composeAction('restartSwarm', scope.row)">
                                        <i class="fa fa-refresh m-r-5"></i>{{$t('global.restart')}}</el-button>
                                    </el-button-group>
                                    <el-button-group v-else>
                                        <el-button type="success" size="mini"
                                         @click="composeAction('startCompose', scope.row)"
                                         :disabled="swarmAvailable||!dockerAvailable"><i class="fa fa-play m-r-5"></i>{{$t('global.start')}}</el-button>
                                        <el-button type="danger" size="mini"  
                                        @click="composeAction('stopCompose', scope.row)"
                                        :disabled="swarmAvailable||!dockerAvailable"><i class="fa fa-stop m-r-5"></i>{{$t('global.stop')}}</el-button>
                                        <el-button type="primary" size="mini"  
                                        @click="composeAction('restartCompose', scope.row)"
                                        :disabled="swarmAvailable||!dockerAvailable"><i class="fa fa-refresh m-r-5"></i>{{$t('global.restart')}}</el-button>
                                    </el-button-group>
                                </div>
                            </template>
                        </el-table-column>
                        <el-table-column
                        width="50px"
                        align="left"
                        >
                            <template slot-scope="scope">
                                <i class="fa fa-angle-right fa-2x m-r-10 pointer c-primary"
                                 @click="goContainer(scope.row)"
                                 v-if="(dockerAvailable&&scope.row.type == 'Compose')
                                 ||(scope.row.type=='Swarm'&&swarmAvailable)"></i>
                            </template>
                        </el-table-column>
                    </el-table>
                </el-tab-pane>
                <el-tab-pane name="composeStatusHistory">
                    <span slot="label">
                        <i class="fa fa-history m-r-5" aria-hidden="true"></i>
                        {{$t('dockerControl.history')}}
                    </span>
                    <compose-history
                    :statusType="singleComposeType"
                    ref="composeHistory"
                    @isFinished="isFinished"
                    :selectedAgentId="selectedAgentId"
                    >
                    </compose-history>
                </el-tab-pane>
            </el-tabs>
            <el-dialog  :visible.sync="dialogYamlVisible">
                <div slot="title" class="dialog-title">
                    <i class="fa fa-file-text m-r-10" aria-hidden="true"></i>
                    {{$t('dockerControl.fileContent')}}
                </div>
                <div class="text-center" v-loading="yamlLoading">
                    <editor v-model="content" @init="editorInit" lang="html" theme="chrome"  height="550"
                    :options="{
                    enableBasicAutocompletion: true,
                    enableSnippets: true,
                    enableLiveAutocompletion: true,
                    tabSize:2,
                    fontSize:14,
                    showPrintMargin:false,   //
                    }"></editor>
                </div>
                
                <div slot="footer" class="dialog-footer">
                    <el-button @click="dialogYamlVisible = false" size="small">{{$t('global.cancel')}}</el-button>
                </div>
            </el-dialog>
        </div>
    </div>
</template>


<script>
    import {dockerAction, deviceInfo, dockerControl} from '@/assets/js/lwm2mMap'
    import handleResponse from '@/components/restfulapi/handleResponse'
    import {getDeviceStatus, setDeviceStatus, execDeviceStatus } from '@/components/restfulapi/deviceStatusApi';
    import {singleComposeType} from "@/assets/js/constant";
    import composeHistory from "./history"
    export default{
        name: 'composecontrol',
        data(){
            return {
                composeTableData: [],
                listLoading: false,
                yamlLoading: false,
                dialogYamlVisible: false,
                multipleSelection: [],
                content: "",
                activeName: "compose",
                singleComposeType,
                keywords: "",
                swarmAvailable: false,
                dockerAvailable: false
            }
        },
        props: {
            selectedAgentId: {
                type: String,
                default: ""
            },
            isSingleMode: {
                type: Boolean,
                default: true
            },
            //selectedAgent is offline
            isOffline: {
                type: Boolean,
                default: false
            }
        },

        components:{
            composeHistory,
            editor: require('vue2-ace-editor'),
        },

        methods:{
            getDockerStatus(){
                if(!this.isSingleMode) {
                    console.error("Cannot operate in a batch mode");
                    return;
                }
                if(this.selectedAgentId == ""){
                    console.error("selectAgentId is empty");
                    return;
                }

                this.listLoading = true;
                getDeviceStatus(this.selectedAgentId, deviceInfo["deviceCapacity"]).then((obj) => {
                    this.listLoading = false;
                    handleResponse(obj, (res) => {
                        if(res.status == "CONTENT"){
                            if(res.content.value){
                                let dockerinfo = JSON.parse(res.content.value);
                                this.dockerAvailable = dockerinfo.docker == "true"? true: false;
                                this.swarmAvailable = dockerinfo["docker.swarm"] == "true"? true: false;
                            }
                            
                            
                        }
                    })   
                })
            },
            selectItem(val) {
                this.multipleSelection = val;
            },
            getSensorStatus(){
                this.listLoading = true;
                getDeviceStatus(this.selectedAgentId, dockerAction["getCompose"]).then((obj) => {
                    this.listLoading = false;
                    handleResponse(obj, (res) => {
                        if(res.status == "CONTENT"){
                            if(res.content.value){
                                this.composeTableData = JSON.parse(res.content.value);
                            }
                        }
                    })   
                })
            },

            initData(){
                this.dockerAvailable = false;
                this.swarmAvailable = false;
                this.composeTableData = [];
            },

            editorInit: function () {
                require('brace/ext/language_tools') //language extension prerequsite...
                require('brace/mode/html')                
                require('brace/mode/javascript')    //language
                require('brace/mode/less')
                require('brace/theme/twilight')
                require('brace/snippets/javascript') //snippet
            },

            showYmlContent(info){
                this.dialogYamlVisible = true;
                let data = {};
                data.productname = info.name;
                data.version = info.version;
                this.yamlLoading = true;
                getDeviceStatus(this.selectedAgentId, dockerAction["getComposeContent"], data).then((obj) => {
                    this.yamlLoading = false;
                    handleResponse(obj, (res) => {
                        if(res.status == "CONTENT"){
                                this.content = res.content.value;
                        }   
                    })   
                })
            },
            goContainer(row){
                if(row.type == "Swarm"){
                    this.$router.push({
                    path: "/main/control/servicecontrol",
                    query: {
                            name: row.name,
                            version: row.version,
                            endpoint: this.selectedAgentId,
                            limited: row.control == 'Limited'
                        }
                    })
                }else{
                    this.$router.push({
                    path: "/main/control/containercontrol",
                    query: {
                            name: row.name,
                            version: row.version,
                            endpoint: this.selectedAgentId
                        }
                    })
                }
                
            },

            handleClick(tab){
                if(tab.name === "composeStatusHistory"){
                    this.isDisabledOffDev = false;
                }else{
                    this.isDisabledOffDev = true;
                }
            },

            isFinished(){
                this.getSensorStatus();  
            },

            actionComposeSensor(cid, appdata){
                if(dockerAction[cid] === undefined){
                    this.$swal("", this.$t('global.functionNotSupport'), 'info', {button: this.$t('global.confirm')});
                    return;
                }
                if(!this.selectedAgentId){
                    this.$swal("",this.$t('global.selectDevice'),"info", {button: this.$t('global.confirm')})
                    return;
                }

                _g.swalWarnDo(this.$t('global.areYouSure'), this.$t("dockerAction."+cid), this.$t('global.confirm'), this.$t('global.cancel')).then((willfunc) => {
                    if (willfunc) {
                        _g.openGlobalLoading();
                        execDeviceStatus(this.selectedAgentId, dockerAction[cid], appdata).then((data) => {
                            handleResponse(data, (res) => {
                                if(res.status == "CHANGED"){
                                    this.activeName = "composeStatusHistory";
                                    this.$refs.composeHistory.intervalGetComposeStatusHistory();
                                }else{
                                    _g.handleError(res)
                                }
                            })
                        })
                    }
                })
            },

            composeAction(cid, selectedAppData){
                let taskname = this.singleComposeType+ (new Date()).getTime();
                let composedata = {
                    taskname,
                    projectname: selectedAppData.name,
                    version: selectedAppData.version,
                }
                this.actionComposeSensor(cid, composedata)
            },

            actionSwarm(cid){
                if(this.selectedAgentId == ""){
                    console.error("selectAgentId is empty");
                    return;
                }
                _g.swalWarnDo(this.$t('global.areYouSure'), this.$t("dockerControl."+cid), this.$t('global.confirm'), this.$t('global.cancel')).then((willfunc) => {
                    if (willfunc) {
                        let obj = {
                            value: this.swarmAvailable
                        }
                        setDeviceStatus(this.selectedAgentId, dockerControl[cid], JSON.stringify(obj), true).then((data) => {
                            handleResponse(data, (res) => {
                                if(res.status == "CHANGED"){
                                    this.$swal("",this.$t('global.success'),"success", {button: this.$t('global.confirm')}).then(() => {
                                        // this.getDockerStatus();                        
                                    })
                                }else{
                                    _g.handleError(res);
                                    this.swarmAvailable = this.swarmAvailable? false: true;
                                }
                            })
                        })
                    }else{
                        this.swarmAvailable = this.swarmAvailable? false: true;
                    }
                }) 
            },
        },

        watch: {
            isSingleMode(val){
                if(val){
                    this.initData();
                }
            },
            selectedAgentId(val){
                if(this.isSingleMode) {
                    this.initData();

                    if(val&& !this.isOffline){
                        this.getSensorStatus();
                        this.getDockerStatus();
                        // this.$refs.dockerHistory.intervalGetDockerStatusHistory();
                    }
                }
            },
           
        },
        computed: {
            isShowSearchInput(){
                if(this.activeName === "compose"){
                    return true;
                }else{
                    return false;
                }
            }
        }

    }
</script>


<style lang="scss" scoped>

    .search{
        position: absolute;
        top: 23px;
        right: 15px;
        width: 230px;
        z-index: 1001;
        
        .search-icon{
            position: absolute;
            top: 6px;
            right: 1px;
            color: #ddd;
            z-index: 10001;
        }
    }
</style>



