<template>
    <div v-show="!isSingleMode">
        <div class="table-box m-b-10 p-b-5" v-loading="listLoading">
            <el-form :inline="true" class="demo-form-inline">
                <el-form-item :label="$t('label.dockerStatus')">
                    <span class="c-success bold m-r-10" v-if="dockerAvailable">{{$t('global.running')}}</span>
                    <span class="c-danger bold m-r-10" v-else>{{$t('global.stop')}}</span>
                </el-form-item>
                <el-form-item :label="$t('label.swarmStatus')" v-show="dockerAvailable">
                    <span class="c-success bold m-r-10" v-if="swarmAvailable">{{$t('global.running')}}</span>
                    <span class="c-danger bold m-r-10" v-else>{{$t('global.stop')}}</span>
                </el-form-item>
            </el-form>
        </div>
        <div class="table-box pkgStatusContent p-r">
            <div class="search" v-show="isShowSearchInput"> 
                <el-input size="small" class="w-250 m-l-10" v-model="keywords" :placeholder="$t('dockerControl.keywordHolder')" >  
                </el-input>
                <i class="fa fa-search search-icon" aria-hidden="true"></i>
            </div>
            <el-tabs v-model="activeName" @tab-click="handleClick">
                <el-tab-pane name="compose">
                    <span slot="label">
                        <i class="fa fa-sellsy m-r-5" aria-hidden="listLoading"></i>
                        {{$t('dockerControl.stack')}}
                        <el-tooltip class="item" effect="light" :content="$t('dockerControl.swarmTooltip')" placement="top-start" >
                            <i class="fa fa-question-circle tooltip m-l-5" aria-hidden="true"></i>
                        </el-tooltip>
                    </span>
                    <el-table
                    :data="composeTableData.filter(data => !keywords || data.name.toLowerCase().includes(keywords.toLowerCase()))"
                    tooltip-effect="dark"
                    style="width: 100%"
                    v-loading="listLoading">
                        <el-table-column
                        label=""
                        width="100"
                        >
                            <template slot-scope="scope">
                                <div>
                                    <img src="@/assets/imgs/compose.png" alt="no img" style="width:48px"/>
                                </div>
                            </template>
                        </el-table-column>
                        <el-table-column
                        :label="$t('dockerControl.projectName')"
                        min-width="150"
                        sortable
                        >
                            <template slot-scope="scope"> 
                                <div class="pointer url" @click="goContainer(scope.row)"
                                v-if="(dockerAvailable&&scope.row.type == 'Compose')||(scope.row.type=='Swarm'&&swarmAvailable)">
                                    {{scope.row.name}}
                                </div>
                                <div v-else>
                                    {{scope.row.name}}
                                </div>
                            </template>
                        </el-table-column>
                        <el-table-column
                        prop="version"
                        :label="$t('dockerControl.version')"
                        min-width="120"
                        sortable>
                        </el-table-column>
                        <el-table-column
                        prop="type"
                        :label="$t('dockerControl.type')"
                        min-width="120"
                        sortable>
                        </el-table-column>
                        <el-table-column
                        :label="$t('dockerControl.origin')"
                        min-width="120"
                        sortable>
                            <template slot-scope="scope"> 
                                <div>
                                    <span  v-if="scope.row.control == 'Limited'">
                                        {{$t('dockerControl.external')}}
                                        <el-tooltip class="item" effect="light" :content="$t('dockerControl.externalTooltip')" placement="top-start" >
                                            <i class="fa fa-question-circle tooltip" aria-hidden="true"></i>
                                        </el-tooltip>
                                    </span>
                                    <span v-else>
                                        {{$t('dockerControl.internal')}}
                                    </span>
                                </div>
                            </template>
                        </el-table-column>
                        <el-table-column
                        :label="$t('dockerControl.state')"
                        min-width="120"
                        >
                            <template slot-scope="scope"> 
                                <span class="c-success m-l-2 m-r-2">{{scope.row.dockerrunning}}</span>
                                /<span class="c-primary m-l-2">{{scope.row.dockernum}}</span>
                            </template>
                        </el-table-column>
                        <el-table-column
                            :label="$t('dockerControl.view')"
                            min-width="100">
                            <template slot-scope="scope"> 
                                <div>
                                    <i class="fa fa-eye pointer c-darkgray" aria-hidden="true"  v-if="scope.row.control == 'Limited'"></i>
                                    <i class="fa fa-eye pointer c-primary" aria-hidden="true" @click="showYmlContent(scope.row)" v-else></i>
                                </div>
                            </template>
                        </el-table-column>
                        <el-table-column
                            :label="$t('dockerControl.action')"
                            min-width="250">
                            <template slot-scope="scope"> 
                                <div>
                                    <el-button-group v-if="scope.row.type == 'Swarm'">
                                        <el-button type="success" size="mini"
                                        :disabled="scope.row.control == 'Limited'||!swarmAvailable" 
                                        @click="selectBatchinstall('startSwarm', scope.row)">
                                        <i class="fa fa-play m-r-5"></i>{{$t('global.start')}}</el-button>
                                        <el-button type="danger" size="mini"
                                        :disabled="scope.row.control == 'Limited'||!swarmAvailable" 
                                        @click="selectBatchinstall('stopSwarm', scope.row)">
                                        <i class="fa fa-stop m-r-5"></i>{{$t('global.stop')}}</el-button>
                                        <el-button type="primary" size="mini"
                                        :disabled="scope.row.control == 'Limited'||!swarmAvailable"  
                                        @click="selectBatchinstall('restartSwarm', scope.row)">
                                        <i class="fa fa-refresh m-r-5"></i>{{$t('global.restart')}}</el-button>
                                    </el-button-group>
                                    <el-button-group v-else>
                                        <el-button type="success" size="mini"  
                                        @click="selectBatchinstall('startCompose', scope.row)"
                                        :disabled="swarmAvailable||!dockerAvailable">
                                        <i class="fa fa-play m-r-5"></i>{{$t('global.start')}}</el-button>
                                        <el-button type="danger" size="mini"  
                                        @click="selectBatchinstall('stopCompose', scope.row)"
                                        :disabled="swarmAvailable||!dockerAvailable">
                                        <i class="fa fa-stop m-r-5"></i>{{$t('global.stop')}}</el-button>
                                        <el-button type="primary" size="mini"  
                                        @click="selectBatchinstall('restartCompose', scope.row)"
                                        :disabled="swarmAvailable||!dockerAvailable">
                                        <i class="fa fa-refresh m-r-5"></i>{{$t('global.restart')}}</el-button>
                                    </el-button-group>
                                </div>
                            </template>
                        </el-table-column>
                        <el-table-column
                        width="50px"
                        align="left"
                        >
                            <template slot-scope="scope">
                                <i class="fa fa-angle-right fa-2x m-r-10 pointer c-primary"
                                 @click="goContainer(scope.row)"
                                  v-if="(dockerAvailable&&scope.row.type == 'Compose')
                                 ||(scope.row.type=='Swarm'&&swarmAvailable)"></i>
                            </template>
                        </el-table-column>
                    </el-table>
                </el-tab-pane>
                <el-tab-pane name="composeStatusHistory">
                    <span slot="label">
                        <i class="fa fa-history m-r-5" aria-hidden="true"></i>
                        {{$t('dockerControl.history')}}
                    </span>
                    <compose-history
                    :statusType="batchComposeType"
                    ref="composeHistory"
                    @isFinished="isFinished"
                    :groupname="groupname"
                    @getStatusCount="getBatchComposeStatusCount"
                    >
                    </compose-history>
                </el-tab-pane>
            </el-tabs>
            <el-dialog :visible.sync="dialogTasknameVisible" @close="initDockerInfo()" :close-on-click-modal="false">
                <div slot="title" class="dialog-title">
                    <i class="fa fa-level-up m-r-10" ></i>
                    {{$t('dockerControl.batchInstall')}}
                </div>
                <el-form :model="composeStatusForm" :rules="rules" ref="composeStatusForm">
                    <el-form-item :label="$t('label.taskName')" label-width="230px" prop="taskname">
                        <el-input v-model.trim="composeStatusForm.taskname" autocomplete="off" class="max-w-350" :placeholder="$t('dockerControl.taskName')"></el-input>
                    </el-form-item>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click="dialogTasknameVisible = false">{{$t('global.cancel')}}</el-button>
                    <el-button type="primary" @click="batchinstall()">{{$t('global.confirm')}}</el-button>
                </div>
            </el-dialog>

            <el-dialog  :visible.sync="dialogYamlVisible">
                <div slot="title" class="dialog-title">
                    <i class="fa fa-file-text m-r-10" aria-hidden="true"></i>
                    {{$t('dockerControl.fileContent')}}
                </div>
                <div class="text-center">
                    <editor v-model="content" @init="editorInit" lang="html" theme="chrome"  height="550"
                    :options="{
                    enableBasicAutocompletion: true,
                    enableSnippets: true,
                    enableLiveAutocompletion: true,
                    tabSize:2,
                    fontSize:14,
                    showPrintMargin:false,   //
                    }"></editor>
                </div>
                
                <div slot="footer" class="dialog-footer">
                    <el-button @click="dialogYamlVisible = false" size="small">{{$t('global.cancel')}}</el-button>
                </div>
            </el-dialog>

            <div class="appStatusCount" v-show="isShowOverview">
                <span>{{$t('label.taskName')}} {{compose_taskname}}</span>
                <span>{{$t('dockerControl.total')}} {{total}}</span>
                <span>{{$t('dockerControl.deploying')}} {{installing}}</span>
                <span>{{$t('dockerControl.success')}} {{success}}</span>
                <span>{{$t('dockerControl.failed')}} {{failed}}</span>
            </div>
        </div>
    </div>
    
    
</template>


<script>
    import {dockerAction, deviceInfo} from '@/assets/js/lwm2mMap'
    import handleResponse from '@/components/restfulapi/handleResponse'
    import {getDeviceStatus, setDeviceStatus, execDeviceStatus, batchExecDeviceStatus} from '@/components/restfulapi/deviceStatusApi';
    import {singleComposeType, batchComposeType} from "@/assets/js/constant";
    import{getAppStatusOverviewApi} from "@/components/restfulapi/appStatusApi"
    import composeHistory from "./history"
    import {setLocal, getLocal, getSession, checkSession, setSession} from "@/assets/js/storage"
    export default{
        name: 'composecontrol',
        data(){
            let validateTaskname = (rule,value,callback)=>{
                let reg=new RegExp(/^(?![._-\s])[\u4E00-\u9FA5A-Za-z0-9_\-\.\s]{1,12}$/);
                if(!reg.test(value)){
                    return callback(new Error (this.$t('global.verifyShortError')))
                }else{callback()};
            };
            return {
                selectedAgentId: '',
                composeTableData: [],
                listLoading: false,
                yamlLoading: false,
                dialogYamlVisible: false,
                dialogTasknameVisible: false,
                multipleSelection: [],
                content: "",
                activeName: "compose",
                batchComposeType,
                // history
                productname: '',
                version: '',
                //batch install
                composeStatusForm:{
                    taskname: '',
                },
                cid: "",
                 //installstatus count show
                isShowOverview: false,
                compose_taskname: '',
                success: 0,
                installing: 0,
                failed: 0,
                listLoading: false,
                keywords: '',
                rules:{
                    taskname: {required: true, validator: validateTaskname, trigger: 'blur'}
                },
                swarmAvailable: false,
                dockerAvailable: false
            }
        },

        components:{
            composeHistory,
            editor: require('vue2-ace-editor'),
        },

        props:{
            selectedAgentsData: {
                type: Array,
                default: []
            },
            groupname: {
                type: String,
                default: ""
            }, 
            isSingleMode: {
                type: Boolean,
                default: true
            }
        },
        methods:{
            getDockerStatus(){
                if(this.selectedAgentsData.length == 0){
                    console.error("getSensorStatus: agentData is null");
                    return;
                }

                this.listLoading = true;
                getDeviceStatus(this.selectedAgentsData[0].value, deviceInfo["deviceCapacity"]).then((obj) => {
                    this.listLoading = false;
                    handleResponse(obj, (res) => {
                        if(res.status == "CONTENT"){
                            if(res.content.value){
                                let dockerinfo = JSON.parse(res.content.value);
                                this.dockerAvailable = dockerinfo.docker == "true"? true: false;
                                this.swarmAvailable = dockerinfo["docker.swarm"] == "true"? true: false;
                            }
                            
                            
                        }
                    })   
                })
            },

            selectItem(val) {
                this.multipleSelection = val;
            },
            getSensorStatus(){
                if(this.isSingleMode) {
                    console.error("Cannot operate in a single mode");
                    return;
                }
                if(this.selectedAgentsData.length == 0){
                    console.error("getSensorStatus: agentData is null");
                    return;
                }
                this.listLoading = true;
                getDeviceStatus(this.selectedAgentsData[0].value, dockerAction["getCompose"]).then((obj) => {
                    this.listLoading = false;
                    handleResponse(obj, (res) => {
                        if(res.status == "CONTENT"){
                            if(res.content.value){
                                this.composeTableData = JSON.parse(res.content.value);
                            }
                            
                        }
                    })   
                })
            },


            initDockerInfo(){
                this.productname = "";
                this.version = "";
                this.composeStatusForm.taskname = "";
                this.cid="";
            },
            initData(){
                this.dockerAvailable = false;
                this.swarmAvailable = false;
                this.composeTableData = [];
            },

            editorInit: function () {
                require('brace/ext/language_tools') //language extension prerequsite...
                require('brace/mode/html')                
                require('brace/mode/javascript')    //language
                require('brace/mode/less')
                require('brace/theme/twilight')
                require('brace/snippets/javascript') //snippet
            },

            showYmlContent(info){
                this.dialogYamlVisible = true;
                let data = {};
                data.productname = info.name;
                data.version = info.version;
                this.yamlLoading = true;
                getDeviceStatus(this.selectedAgentsData[0].value, dockerAction["getComposeContent"], data).then((obj) => {
                    this.yamlLoading = false;
                    handleResponse(obj, (res) => {
                        if(res.status == "CONTENT"){
                                this.content = res.content.value;
                        }   
                    })   
                })
            },
            
            goContainer(row){
                if(row.type == "Swarm"){
                    this.$router.push({
                    path: "/main/control/servicecontrol",
                    query: {
                            name: row.name,
                            version: row.version,
                            endpoint: this.selectedAgentsData[0].value,
                            limited: row.control == 'Limited'
                        }
                    })
                }else{
                    this.$router.push({
                    path: "/main/control/containercontrol",
                    query: {
                            name: row.name,
                            version: row.version,
                            endpoint: this.selectedAgentsData[0].value, 
                        }
                    })
                }
            },

            handleClick(tab){
                if(tab.name === "composeStatusHistory"){
                    this.getBatchComposeStatusCount();
                }else{
                    this.isShowOverview = false;
                }
            },

            isFinished(){
                this.getSensorStatus();
            },

            actionComposeSensor(cid, appdata){
                if(dockerAction[cid] === undefined){
                    this.$swal("", this.$t('global.functionNotSupport'), 'info', {button: this.$t('global.confirm')});
                    return;
                }
                _g.openGlobalLoading();
                let taskname = this.composeStatusForm.taskname;
                this.dialogTasknameVisible = false;
                batchExecDeviceStatus(dockerAction[cid], appdata).then((data) => {
                    handleResponse(data, (res) => {
                        if(res.status == "CHANGED"){
                            setLocal("compose_taskname", taskname);
                            this.activeName = "composeStatusHistory";
                            this.$refs.composeHistory.intervalGetComposeStatusHistory();
                        }else{
                            _g.handleError(res)
                        }
                    })
                })
                    
                
            },
            batchinstall(){
                if(this.selectedAgentsData.length === 0){
                    this.$swal("", this.$t('global.devicesOffline'),"info", {button: this.$t('global.confirm')});
                    return;
                }
                this.$refs.composeStatusForm.validate((valid) => {
                    if(valid) {
                        let composedata = {
                            taskname: this.composeStatusForm.taskname,
                            projectname: this.productname,
                            version: this.version,
                            groupname: this.groupname,
                        }
                        this.actionComposeSensor(this.cid, composedata);
                        
                    }
                })
            },

            back() {
                this.$router.go(-1);
            },

            selectBatchinstall(type, selectedAppData){
                this.cid = type;
                this.productname = selectedAppData.name;
                this.version = selectedAppData.version;
                this.composeStatusForm.taskname = "";
                this.dialogTasknameVisible = true;
            },

              //get updatedp status count
            getBatchComposeStatusCount(){
                let compose_taskname = getLocal("compose_taskname");
                if(!compose_taskname){
                    console.error("getBatchComposeStatusCount:localStorage(compose_taskname) is null")
                    this.isShowOverview = false;
                    return;
                }
                getAppStatusOverviewApi(compose_taskname, batchComposeType).then((data) => {
                    handleResponse((data), (res) => {
                        if(res){
                            this.compose_taskname = compose_taskname;

                            this.installing = res.installing;
                            this.failed = res.fail;
                            this.success = res.success;
                            if(this.total > 0 &&  this.activeName == "composeStatusHistory"){
                                this.isShowOverview = true;
                            }else{
                                this.isShowOverview = false;
                            }
                        }
                    })
                })
            },
        },

        watch: {
            isSingleMode(val){
                if(val){
                    this.initData();
                }
            },
            groupname(val){
                if(!this.isSingleMode&& val){
                    this.initData();
                    this.getSensorStatus();
                    this.getDockerStatus();
                }
            }
           
        },
        computed: {
            isShowSearchInput(){
                if(this.activeName === "compose"){
                    return true;
                }else{
                    return false;
                }
            },
            total(){
                return  this.success+ this.installing+ this.failed;
            }
        }

    }
</script>


<style lang="scss" scoped>
@import "@/assets/css/colors.scss";
    .pkgStatusContent{
        position: relative;
        .appStatusCount{
            position: absolute;
            top: 10px;
            right: 6px;
            span{
                font-weight: 500;
                font-size: 11px;
                &:nth-child(1){
                    color: $primary-color;
                }
                &:nth-child(2){
                    border-left: 2px solid #ddd;
                    padding-left: 3px; 
                    color: $info-color;
                }
                &:nth-child(3){
                    color: $warn-color;
                }
                &:nth-child(4){
                    color: $success-color;
                }
                &:nth-child(5){
                    color: $darkgray-color;
                }
            }
            
        }
    }

    .search{
        position: absolute;
        top: 23px;
        right: 15px;
        width: 230px;
        z-index: 1001;
        .search-icon{
            position: absolute;
            top: 6px;
            right: 1px;
            color: #ddd;
            z-index: 10001;
        }
    }
</style>



